interface CodeMap {
    id: number,
    code: string,
    link: string
}

export async function createLink(link: string): Promise<CodeMap | null> {
    const encoded = encodeURI(link);
    const response = await fetch(`/create?link=${encoded}`, {
        method: "POST"
    });

    if (!response.ok) {
        console.error("Error:", response.statusText);
        return null;
    }

    const newCodeMap = await response.json(); // Parse the JSON response
    return newCodeMap; // Return the created CodeMap object if needed
}
