import { Box, Button, Stack, TextField, ThemeProvider } from "@mui/material";
import "./App.css";
import theme from "./theme";
import { createLink } from "./api";
import { useState } from "react";

function App() {
    const [currentLink, setCurrentLink] = useState("");
    const [shortCode, setShortCode] = useState("");

    const HandleShorten = async () => {
        const codeMap = await createLink(currentLink);

        if (codeMap?.code) {
            setShortCode(codeMap.code);
        }
    };

    return (
        <ThemeProvider theme={theme}>
            <div>
                <center>
                    <Box sx={{ width: 400 }}>
                        <Stack
                            useFlexGap
                            direction="row"
                            spacing={{ xs: 1, sm: 2 }}
                            justifyContent="center"
                            sx={{
                                flexWrap: "wrap",
                            }}
                        >
                            <TextField
                                placeholder="Link here..."
                                required
                                onChange={(e) => {
                                    setCurrentLink(e.target.value);
                                }}
                                onKeyDown={async (e) => {
                                    if (e.key === "Enter") {
                                        HandleShorten()
                                    }
                                }}
                                InputProps={{ sx: { borderRadius: 4 } }}
                            />
                            <Button
                                className="button"
                                variant="contained"
                                onClick={HandleShorten}
                            >
                                Shorten URL
                            </Button>
                            {shortCode != "" && (
                                <a href={`/${shortCode}`}>
                                    /{shortCode}
                                </a>
                            )}
                        </Stack>
                    </Box>
                </center>
            </div>
        </ThemeProvider>
    );
}

export default App;
